<script>
export default {
  data () {
    return {
      images: []
    };
  },
  mounted () {
    this.getForm(this.$route.params.id);
  },
  methods: {
    getForm (id) {
      var recent = {
        what: "middaymvrview",

        params: {
          form_id: id
        }
      };

      this.$socket
        .makeGetRequest(recent)
        .then(response => {
          this.$store.dispatch('midaymvr', response.data)
          if (response.type == "middaymvrview") {
            response.data.forEach(item => {
              for (let i = 0; i <= 120; i++) {
                if (i == item.questionno) {
                  // console.log(item.questiontext)
                  if (item.questionno == "3") {
                    let stores = this.$store.getters.stores
                    stores.forEach(j => {
                      if (j.id == item.answers) {
                        item.answers = j.address + ", " + j.location;

                      }
                    })
                  }
                  document.getElementById(i).querySelectorAll("p")[0].innerHTML = item.answers
                }

              };
              if (item.questionno.includes('image')) {
                this.images.push(item)
              }
            })
          }
        })
        .catch(e => {
          console.log(e);
        });
    },
    downloadPdf () {
      this.$router.push('/midaymvrpdf')
    }
  }
};
</script>
