<script>

export default {

  mounted () {
    this.getForm();
  },

  methods: {
    getForm () {
      let report = this.$store.getters.krss
      for (let i = 0; i <= 400; i++) {
        report.forEach(item => {
          if (i == item.questionno) {
            if (item.questionno == "2") {
              let stores = this.$store.getters.stores
              stores.forEach(j => {
                if (j.id == item.answers) {
                  item.answers = j.address + ", " + j.location;
                }
              })
            }
            document
              .getElementById(i)
              .querySelectorAll("p")[0].innerHTML = item.answers;
          }
        });
      }
      this.downloadPdf()
    },
    downloadPdf () {
      this.$refs.html2Pdf.generatePdf()
    },
    hasDownloaded (e) {
      this.$router.go(-1)
    }
  }
};
</script>
