<script>
export default {
  data () {
    return {};
  },
  mounted () {
    this.getForm(this.$route.params.id);
  },
  methods: {
    getForm (id) {
      var recent = {
        what: "redbookview",
        // showLoader: "no",
        params: {
          form_id: id
        }
      };

      this.$socket
        .makeGetRequest(recent)
        .then(response => {
          if (response.type == "redbookview") {
            this.$store.dispatch('redbook', response.data)
            for (let i = 0; i <= 300; i++) {
              response.data.forEach(item => {
                if (i == item.questionno) {
                  if (item.questionno == "2") {
                    let stores = this.$store.getters.stores
                    stores.forEach(j => {
                      if (j.id == item.answers) {
                        item.answers = j.address + ", " + j.location;
                      }
                    })
                  }
                  document
                    .getElementById(i)
                    .querySelectorAll("p")[0].innerHTML = item.answers;
                }
              });
            }

          }
        })
        .catch(e => {
          console.log(e);
        });
    },
    downloadPdf () {
      this.$router.push('/redbookpdf')
    }
  }
};
</script>
<style>
table.red thead th {
  font-size: 9px;
  padding: 1px;
